.btn {
  font-size: 12px;
  padding: 8px 26px 6px;
  letter-spacing: 1.2px;
  font-weight: 600;
  text-transform: uppercase;
  border-radius: 2px;
  outline: none;
  -webkit-transition: 0.15s linear;
  transition: 0.15s linear;
}
.btn:hover {
  box-shadow: 0px 2px 10px rgba(0, 0, 0, 0.25);
}
.btn-round {
  border-radius: 10rem;
}
h1,
h2,
h3,
h4,
h2,
h5,
h6,
.h1,
.h2,
.h3,
.h4,
.h5,
.h6 {
  color: #394141;
  /* letter-spacing: 0.5px; */
}

h6,
.h6 {
  font-size: 16px;
  font-weight: 600;
  line-height: 30px;
}

h5,
.h5 {
  font-size: 18px;
  font-weight: 600;
  line-height: 30px;
}

p {
  font-size: 14px;
}

.text-secondary {
  color: #757575 !important;
}

/* nav bar */
.navbar {
  padding: 0px 1rem;
  background: transparent;
  transition: 0.3s;
  z-index: 1000;
}
.navbar.active {
  background-color: white;
  box-shadow: 0 1px 9px rgb(0 0 0 / 5%);
}
.navbar .navbar-nav .nav-link {
  font-weight: 600;
  letter-spacing: 1px;
  font-size: 0.87em;
  word-spacing: 2px;
  padding-left: 20px;
  padding-right: 20px;
}
.navbar-light .navbar-nav .nav-link {
  color: rgba(117, 117, 117, 0.8);
}
.navbar-light .navbar-nav .active > .nav-link,
.navbar-light .navbar-nav .nav-link.active,
.navbar-light .navbar-nav .nav-link.show,
.navbar-light .navbar-nav .show > .nav-link {
  color: #394141;
}
/* collapse */
@media (max-width: 992px) {
  #responsive-navbar-nav {
    width: 300px;
    height: 100%;
    position: fixed;
    top: 0px;
    bottom: 0px;
    left: -300px;
    -webkit-transform: translateX(0);
    transform: translateX(0);
    -webkit-transition: 0.2s ease all;
    transition: 0.2s ease all;
    background: #fff;
    z-index: 1002;
  }
  #responsive-navbar-nav.show {
    -webkit-transform: translateX(300px);
    transform: translateX(300px);
    box-shadow: 0px 0px 50px rgba(0, 0, 0, 0.25);
  }

  #responsive-navbar-nav.show .navbar-nav {
    padding: 40px 0px;
  }

  #responsive-navbar-nav.show .nav-link {
    padding: 15px 20px;
  }
}

/* form */
.form-control {
  font-size: 15px;
  border-top: none;
  border-left: none;
  border-right: none;
  border-radius: 0px;
  padding-left: 0px;
  padding-right: 0px;
  background-color: #fff !important;
}

.form-control:focus {
  background-color: #fff;
  border-color: #4ca456;
  box-shadow: none;
}

.form-group label {
  font-size: 13px;
  color: #757575;
}
